import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { Button, Form } from 'react-bootstrap'
import toast from 'react-hot-toast'
// Common
import Loading from '../common/Loading'
import Error from '../common/Error'
import NoContent from '../common/NoContent'
// Components
import ImageSearchModal from './ImageSearchModal'
// Resources
import defaultBookCover from '../../resources/images/common/book.png'
// Functions
import { editBook } from '../../functions/book'
import { API_BASE_URL } from '../../settings/urls/apiUrl'
import { getToken } from '../../functions/user'

const BookEditForm = () => {
	const { id } = useParams()
	const navigate = useNavigate()
	const BOOK_DETAIL_URL = `/book/detail/${id}`

	// Messages
	const TITLE_MESSAGE = `   `
	const AUTHOR_MESSAGE = `   `
	const PAGE_MESSAGE = `   `
	const EDIT_SUCCESS_MESSAGE = ` `

	const [initalFetch, setInitialFetch] = useState(true)
	const [isLoading, setIsLoading] = useState(true)
	const [notFound, setNotFound] = useState(false)
	const [isError, setIsError] = useState(false)

	const [showModal, setShowModal] = useState(false)
	const openModal = () => {
		title !== '' ? setShowModal(true) : alert('      ')
	}

	// Book Info
	const [title, setTitle] = useState('')
	const [author, setAuthor] = useState('')
	const [endPage, setEndPage] = useState(0)
	const [cover, setCover] = useState('')
	const [language, setLanguage] = useState('KOREAN')
	const [category, setCategory] = useState('LITERATURE')
	const [isSharing, setIsSharing] = useState(false)
	const [form, setForm] = useState('PHYSICAL')
	const [source, setSource] = useState('NOT_PROVIDED')

	useEffect(() => {
		const BOOK_GET_API_URL = `${API_BASE_URL}/v1/book/${id}`

		setTimeout(() => {
			setInitialFetch(false)
		}, 5000)

		fetch(BOOK_GET_API_URL, {
			method: 'GET',
			headers: {
				Authorization: getToken(),
			},
		})
			.then((res) => {
				if (!res.status.toString().startsWith(2)) {
					setNotFound(true)
					return
				}

				return res.json()
			})
			.then((book) => {
				setTitle(book.title)
				setAuthor(book.author)
				setLanguage(book.language)
				setCategory(book.category)
				setEndPage(book.endPage)
				setForm(book.form)
				setSource(book.source)
				setCover(book.cover)
				setIsSharing(book.isSharing)
			})
			.catch(() => setIsError(true))
			.finally(() => {
				setIsLoading(false)
				setInitialFetch(false)
			})
	}, [id])

	const handleEdit = (e) => {
		e.preventDefault()

		const editedBook = {
			bookId: id,
			title: title,
			author: author,
			language: language,
			category: category,
			endPage: endPage,
			form: form,
			soruce: source,
			cover: cover,
			isSharing: isSharing,
		}
		editBook(editedBook).then((result) => {
			if (result) {
				toast.success(EDIT_SUCCESS_MESSAGE)
				navigate(BOOK_DETAIL_URL)
			} else {
				toast.error('       ')
			}
		})
	}

	return (
		<div className='container'>
			{initalFetch ? (
				<></>
			) : isLoading ? (
				<Loading />
			) : isError ? (
				<Error />
			) : notFound ? (
				<NoContent />
			) : (
				<div className='container'>
					<ImageSearchModal showModal={showModal} setShowModal={setShowModal} setCover={setCover} title={title} author={author} />

					<Form onSubmit={handleEdit}>
						<div className='row row-eq-height text-center'>
							<div className='col-12 col-lg-4 mb-3'>
								<img src={cover === '' ? defaultBookCover : cover} alt='' className='img-fluid rounded' />

								<div className='row mt-5'>
									<div className='col-6'>
										<Button className='w-100' onClick={openModal}>
											  
										</Button>
									</div>
									<div className='col-6'>
										<Button className='w-100' disabled>
											 
										</Button>
									</div>
								</div>
							</div>

							<div className='col-12 col-lg-8 mb-3 text-start'>
								<Form.Group className='mb-3'>
									<Form.Label> </Form.Label>
									<Form.Control
										type='text'
										placeholder={TITLE_MESSAGE}
										required
										onChange={(e) => setTitle(e.target.value)}
										value={title}
									/>
								</Form.Group>

								<Form.Group className='mb-3'>
									<Form.Label></Form.Label>
									<Form.Control
										type='text'
										placeholder={AUTHOR_MESSAGE}
										required
										onChange={(e) => setAuthor(e.target.value)}
										value={author}
									/>
								</Form.Group>

								<div className='row'>
									<div className='col-6 col-md-4'>
										<Form.Group className='mb-3' onChange={(e) => setLanguage(e.target.value)}>
											<Form.Label> </Form.Label>
											<Form.Select value={language}>
												<option value='KOREAN'> </option>
												<option value='ENGLISH'> </option>
												<option value='JAPANESE'> </option>
												<option value='CHINESE'> </option>

												<option value='FRENCH'> </option>
												<option value='SPANISH'>  </option>
											</Form.Select>
										</Form.Group>
									</div>

									<div className='col-6 col-md-4'>
										<Form.Group className='mb-3' onChange={(e) => setEndPage(e.target.value)}>
											<Form.Label>  </Form.Label>
											<Form.Control type='number' placeholder={PAGE_MESSAGE} required value={endPage} />
										</Form.Group>
									</div>

									<div className='col-12 col-md-4'>
										<Form.Group>
											<Form.Label></Form.Label>
											<Form.Select onChange={(e) => setCategory(e.target.value)} value={category}>
												<option value='LITERATURE'></option>

												<option value='NATURAL_SCIENCE'></option>
												<option value='SOCIAL_SCIENCE'></option>
												<option value='TECHNOLOGY'></option>

												<option value='PHILOSOPHY'></option>
												<option value='LANGUAGE'></option>
												<option value='ART'></option>
												<option value='HISTORY'></option>

												<option value='RELIGION'></option>

												<option value='OTHERS'></option>
											</Form.Select>
										</Form.Group>
									</div>
								</div>

								<div className='row mt-4'>
									<div className='col-6'>
										<Form.Group className='mb-3' onChange={(e) => setForm(e.target.value)}>
											<Form.Label> </Form.Label>
											<Form.Select value={form}>
												<option value='PHYSICAL'></option>
												<option value='EBOOK'></option>
												<option value='AUDIO'></option>
											</Form.Select>
										</Form.Group>
									</div>

									<div className='col-6'>
										<Form.Group className='mb-3' onChange={(e) => setSource(e.target.value)}>
											<Form.Label>  ?</Form.Label>
											<Form.Select value={source}>
												<option value='NOT_PROVIDED'>  </option>

												<option value='BUY_NEW_OFFLINE'>  -  </option>
												<option value='BIY_NEW_ONLINE'>  -  </option>
												<option value='BUY_USED_OFFLINE'> -   ( )</option>
												<option value='BUY_USED_ONLINE'> -  </option>

												<option value='LIBRARY'></option>
												<option value='BORROW_STORE'>  </option>
												<option value='BORROW_FRIENDS'> </option>

												<option value='SUBSCRIPTION'> (  )</option>

												<opton value='OTHERS'></opton>
											</Form.Select>
										</Form.Group>
									</div>
								</div>

								<Form.Group className='mb-3' controlId='formBasicCheckbox'>
									<Form.Check
										type='checkbox'
										label='       '
										onChange={() => setIsSharing(!isSharing)}
										value={isSharing}
									/>
								</Form.Group>

								<div className='row justify-content-center mt-5'>
									<div className='col-6'>
										<Button variant='danger' onClick={() => navigate(BOOK_DETAIL_URL)} className='w-100'>
											
										</Button>
									</div>
									<div className='col-6'>
										<Button variant='warning' type='submit' className='w-100'>
											
										</Button>
									</div>
								</div>
							</div>
						</div>
					</Form>
				</div>
			)}
		</div>
	)
}

export default BookEditForm
