import React, { useEffect, useState } from 'react'
import toast from 'react-hot-toast'
import { useParams, useNavigate, useLocation } from 'react-router-dom'
import { Button, Card, Pagination, Tab, Tabs } from 'react-bootstrap'
// Components
import Loading from '../common/Loading'
import Error from '../common/Error'
import NoContent from '../common/NoContent'
import HorizontalBookView from './HorizontalBookView'
// Images
import kimchiImage from '../../resources/images/general/kimchi.png'
import bookShelfImage from '../../resources/images/common/bookshelf.png'
// Functions
import { deleteBook, getBookList, giveUpBook, unGiveUpBook } from '../../functions/book'

const BookList = () => {
	const { range } = useParams()
	const { rangeDetail } = useParams()

	const navigate = useNavigate()
	const location = useLocation()

	const [initalFetch, setInitialFetch] = useState(true)
	const [isLoading, setIsLoading] = useState(false)
	const [error, setError] = useState(false)

	const [bookList, setBookList] = useState(null)

	useEffect(() => {
		setTimeout(() => setInitialFetch(false), 5000)

		getBookList(range === 'not-done' ? (rangeDetail === 'all' ? range : rangeDetail) : range)
			.then((list) => {
				console.log(list)
				if (list == null) {
					setError(true)
				} else {
					setBookList(list)
				}
			})
			.finally(() => {
				setInitialFetch(false)
				setIsLoading(false)
			})
	}, [location.pathname, range, rangeDetail])

	return (
		<div className='container-lg'>
			{initalFetch ? (
				<></>
			) : isLoading ? (
				<Loading message='  ' />
			) : error || bookList == null ? (
				<Error />
			) : (
				<>
					{range === 'not-done' ? (
						<Tabs
							className='mb-3'
							id='controlled-tab-example'
							fill
							defaultActiveKey={rangeDetail}
							onSelect={(key) => {
								navigate(`/book/not-done/${key}`)
							}}>
							<Tab eventKey='all' title=''>
								{bookList.length === 0 ? (
									<NoContent
										message={
											range === 'not-done'
												? `    .    !`
												: range === 'done'
												? `    `
												: range === 'give-up'
												? `   ! ${localStorage.getItem('user-name')}   `
												: `  `
										}
										icon={range === 'give-up' ? kimchiImage : bookShelfImage}
									/>
								) : (
									<BookCardList bookList={bookList} range={range} setBookList={setBookList} />
								)}
							</Tab>

							<Tab eventKey='not-started' title='  '>
								{bookList.length === 0 ? (
									<NoContent
										message={
											range === 'not-done'
												? rangeDetail === 'all'
													? `  `
													: rangeDetail === 'not-started'
													? `    `
													: `   `
												: range === 'done'
												? `    `
												: range === 'give-up'
												? `   ! ${localStorage.getItem('user-name')}   `
												: `  `
										}
										icon={range === 'give-up' ? kimchiImage : bookShelfImage}
									/>
								) : (
									<BookCardList bookList={bookList} range={range} setBookList={setBookList} />
								)}
							</Tab>

							<Tab eventKey='started' title=''>
								{bookList.length === 0 ? (
									<NoContent
										message={
											range === 'not-done'
												? `    .    !`
												: range === 'done'
												? `    `
												: range === 'give-up'
												? `   ! ${localStorage.getItem('user-name')}   `
												: `  `
										}
										icon={range === 'give-up' ? kimchiImage : bookShelfImage}
									/>
								) : (
									<BookCardList bookList={bookList} range={range} setBookList={setBookList} />
								)}
							</Tab>
						</Tabs>
					) : bookList.length === 0 ? (
						<NoContent
							message={
								range === 'not-done'
									? `    .    !`
									: range === 'done'
									? `    `
									: range === 'give-up'
									? `   ! ${localStorage.getItem('user-name')}   `
									: `  `
							}
							icon={range === 'give-up' ? kimchiImage : bookShelfImage}
						/>
					) : (
						<BookCardList bookList={bookList} range={range} setBookList={setBookList} />
					)}

					{bookList.length !== 0 && (
						<Pagination className='pagination justify-content-center mb-5'>
							<Pagination.Item key={1} active={true}>
								1
							</Pagination.Item>
						</Pagination>
					)}
				</>
			)}
		</div>
	)
}

const BookCardList = ({ bookList, range, setBookList }) => {
	const navigate = useNavigate()

	return (
		<div className='row row-eq-height'>
			{bookList.map((book) => {
				return (
					<div className='col-12 col-md-6 mb-5'>
						<Card className='h-100'>
							<Card.Body>
								<>
									{range === 'done' ? (
										<HorizontalBookView book={book} link={`/book/detail/${book.bookId}`} />
									) : range === 'give-up' ? (
										<HorizontalBookView
											book={book}
											firstButton={
												<Button
													variant='success'
													className='w-100'
													onClick={() => {
														const confirm = window.confirm('  ?')

														if (confirm) {
															unGiveUpBook(book.bookId).then((success) => {
																if (success) {
																	toast.success('     ')
																	navigate(`/book/detail/${book.bookId}`)
																} else {
																	toast.error(' .     ')
																}
															})
														}
													}}>
													 
												</Button>
											}
											secondButton={
												<Button
													variant='danger'
													className='w-100'
													onClick={() => {
														const confirm = window.confirm('   ?')

														if (confirm) {
															deleteBook(book.bookId).then((success) => {
																if (success) {
																	toast.success(' ')
																	setBookList(bookList.filter((b) => b.bookId !== book.bookId))
																} else {
																	toast.error(' .     ')
																}
															})
														}
													}}>
													
												</Button>
											}
											link={`/book/detail/${book.bookId}`}
										/>
									) : (
										<HorizontalBookView
											book={book}
											link={`/book/detail/${book.bookId}`}
											firstButton={
												<a href={`/reading/${book.bookId}`} className='btn btn-primary w-100'>
													 
												</a>
											}
											secondButton={
												<Button
													variant='danger'
													className='w-100'
													onClick={() => {
														const confirm = window.confirm(' ?')

														if (confirm) {
															giveUpBook(book.bookId).then((success) => {
																if (success) {
																	toast.success(' .      !')
																	setBookList(bookList.filter((b) => b.bookId !== book.bookId))
																} else {
																	toast.error('    ')
																}
															})
														}
													}}>
													  
												</Button>
											}
										/>
									)}
								</>
							</Card.Body>
						</Card>
					</div>
				)
			})}
		</div>
	)
}

export default BookList
