import React, { useState } from 'react'
import { Modal, Button, Form } from 'react-bootstrap'
import toast from 'react-hot-toast'
import { changePassword, requestChangePasswordVerificationCode } from '../../functions/user'
import { ERROR_MESSAGE } from '../../messages/commonMessages'

const ChangePasswordModal = ({ isModalOpen, setIsModalOpen }) => {
	const [oldPassword, setOldPassword] = useState('')
	const [newPassword, setNewPassword] = useState('')
	const [verificationCode, setVerificationCode] = useState('')

	const [isVerificationRequested, setIsVerificationRequested] = useState(false)

	const handleRequestVerificationCode = () => {
		toast.loading('  ')

		requestChangePasswordVerificationCode().then((success) => {
			if (success) {
				toast.success(' ')
				setIsVerificationRequested(true)
			} else {
				toast.error(ERROR_MESSAGE)
			}
		})
	}

	const handleChangePassword = (e) => {
		e.preventDefault()

		if (!isVerificationRequested) {
			toast.error('   ')
			return
		}

		if (verificationCode === '') {
			toast.error('  ')
			return
		}

		if (verificationCode < 5) {
			toast.error('  ')
			return
		}

		if (oldPassword === '') {
			toast.error('   ')
			return
		}

		if (newPassword === '') {
			toast.error('   ')
			return
		}

		if (oldPassword === newPassword) {
			toast.error('    ')
			return
		}

		if (newPassword.length < 6) {
			toast.error('6    ')
			return
		}

		toast.loading('  ')

		changePassword(verificationCode, oldPassword, newPassword).then((status) => {
			if (status.startsWith(2)) {
				toast.success(' ')
				closeModal()
			} else if (status === '400') {
				toast.error('  .   ')
			} else {
				toast.error(ERROR_MESSAGE)
			}
		})
	}

	const closeModal = () => {
		setOldPassword('')
		setNewPassword('')
		setVerificationCode('')
		setIsVerificationRequested(false)
		setIsModalOpen(false)
	}

	return (
		<Modal show={isModalOpen} onHide={() => closeModal} fullscreen='md-down' backdrop='static'>
			<Modal.Header className='text-center' closeButton>
				<h4 className='w-100'> </h4>
			</Modal.Header>

			<Modal.Body>
				<Form onSubmit={(e) => handleChangePassword(e)}>
					<Form.Control
						className='mb-2'
						placeholder='  '
						onChange={(e) => setVerificationCode(e.target.value)}
						disabled={!isVerificationRequested}
					/>
					<Button
						className='mb-3 w-100'
						variant='primary'
						onClick={() => handleRequestVerificationCode()}
						disabled={isVerificationRequested}>
						 
					</Button>

					<Form.Control
						type='password'
						className='mb-2'
						placeholder='   '
						onChange={(e) => setOldPassword(e.target.value)}
					/>
					<Form.Control
						type='password'
						className='mb-2'
						placeholder='   '
						onChange={(e) => setNewPassword(e.target.value)}
					/>

					<Button className='mb-2 mt-2 w-100' variant='success' type='submit'>
						 
					</Button>
				</Form>
			</Modal.Body>
		</Modal>
	)
}

export default ChangePasswordModal
